import React, { Component} from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';


import * as Helpers from '../helpers';
import {ThreeSetup,Environment} from '../components';



import '../../../scss/main.scss'
import '../styles.scss'

class Webgl3d extends Component  {

  constructor(props){
    super(props)
    this.sceneRef = React.createRef();
    this.animationId = null;
  }

  componentDidMount() {
    document.title = "example || WebGL";

    this.threeSetup = new ThreeSetup(this.sceneRef)
    this.threeSetup.init();
    this.initThree()
    window.addEventListener('resize', this.threeSetup.handleResize);
  }

  componentWillUnmount(){
    document.title = "Canvas Generative";
    window.removeEventListener('resize', this.threeSetup.handleResize);
    cancelAnimationFrame(this.animationId);
    this.threeSetup.dispose();
  }

  initThree() {
    let scene = this.threeSetup.scene;
    const geometry = new THREE.PlaneGeometry( 25, 25 );

    const material1 = Helpers.Material.linearGradient(0x355C5F,0xED6065,1)
    const plane1 = new THREE.Mesh( geometry, material1 );
    plane1.position.set(-15,15);
    scene.add( plane1 );

    const material2 = Helpers.Material.linearGradient2(0x355C5F,0xED6065,0.0)
    const plane2 = new THREE.Mesh( geometry, material2 );
    plane2.position.set(15,15);
    scene.add( plane2 );

    const material3 = new THREE.MeshBasicMaterial({
      map: Helpers.Textures.gradientColor(0x355C5F,0xED6065),
      metalness: 1,
      roughness: 0.2,
    });
    const plane3 = new THREE.Mesh( geometry, material3 );
    plane3.position.set(-15,-15);
    scene.add( plane3 );

    const material4 = new THREE.MeshBasicMaterial({
      map: Helpers.Textures.gradientColor(0xED6065,0x355C5F),
      metalness: 1,
      roughness: 0.2,
    });
    const plane4 = new THREE.Mesh( geometry, material4 );
    plane4.position.set(15,-15);
    scene.add( plane4 );


    // const material4 = Helpers.Material.linearGradient3(0x355C5F,0xED6065)
    // const plane4 = new THREE.Mesh( geometry, material4 );
    // plane4.position.set(15,-15);
    // scene.add( plane4 );

    Environment(scene)
    this.animate();
  }

  animate = () => {
    this.animationId = requestAnimationFrame(this.animate);
    this.threeSetup.render()
  }


  switchPlatform = (index) =>{
    // const urlParams = new URLSearchParams(window.location.search);
    // urlParams.set("platform", index)
    // let params = urlParams.toString();
    // let url = `${window.location.pathname}?${params}`;
    // window.location.href = url;
  }

  render(){
    return (
      <div className="section__model main__margin">
        <div className="main__panel">
          <div className="canvas__panel" ref={this.sceneRef}>
            <canvas/>
          </div>
          <div className="canvas__btns">
            <button onClick={()=>this.switchPlatform(1)}>1</button>
            <button onClick={()=>this.switchPlatform(2)}>2</button>
            <button onClick={()=>this.switchPlatform(3)}>3</button>
          </div>
        </div>
      </div>
    )
  }
}

export default Webgl3d;
